body {
  color: rgba(228, 193, 104, .835);
  max-width: 80vw;
  background-color: #000;
  flex-direction: column;
  align-items: center;
  margin: auto;
  display: flex;
}

a {
  height: 20vw;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.photo-card {
  flex-wrap: wrap;
  display: flex;
}

.img-div {
  flex-direction: column;
  flex-basis: calc(25% - 18px);
  margin: auto;
  padding-top: 20px;
  display: flex;
}

.link-div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.intro {
  animation: moveContainer .75s ease-out forwards;
}

.img-block {
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: .6;
  filter: grayscale(40%);
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  transition: transform 1s ease-in-out, opacity 1s, filter 1s ease-in;
  display: block;
}

.img-block:hover {
  opacity: 1;
  filter: grayscale(0%);
  transform: scale(1.1);
}

.info {
  background-color: rgba(90, 114, 133, .635);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  flex-wrap: wrap;
  display: flex;
}

.info-item {
  flex-basis: calc(50% - 12px);
  justify-content: center;
  display: flex;
}

.overflow-hidden {
  overflow: hidden;
}

.drop-in, .drop-in-2 {
  animation: drop-in 2.5s .1s backwards;
}

@keyframes drop-in {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.searchButton {
  height: 20px;
  background-color: #000;
  border-style: none;
  font-size: 25px;
  transition: transform .5s ease-in-out;
}

.searchButton:hover {
  transform: scale(1.3);
}

.lens {
  fill: rgba(228, 193, 104, .835);
  transition: fill .25s ease-in-out;
}

.lens:hover {
  fill: rgba(234, 179, 41, .835);
}

input {
  width: 80vw;
  max-width: 400px;
  color: rgba(234, 179, 41, .835);
  background: 0;
  border: 0;
  outline: none;
  font-size: 1.5em;
  transition: padding .3s .2s;
}

input:focus {
  padding-bottom: 5px;
}

input:focus + .line:after {
  transform: scaleX(1);
}

.field {
  max-width: 1200px;
  margin-top: 20px;
  margin-bottom: 20px;
  position: relative;
}

.field .line {
  width: 100%;
  height: 3px;
  background: rgba(234, 179, 41, .424);
  position: absolute;
  bottom: -8px;
}

.field .line:after {
  content: " ";
  float: right;
  width: 100%;
  height: 3px;
  background: rgba(234, 179, 41, .835);
  transition: transform .3s;
  position: absolute;
  transform: scaleX(0);
}

.search-form {
  display: flex;
}

.scroll-down {
  position: fixed;
  bottom: 0;
  right: 5vw;
}

.arrows {
  width: 60px;
  height: 72px;
  margin-left: -30px;
  position: absolute;
  bottom: 20px;
  left: 50%;
}

.arrows path {
  stroke: #6f6d32;
  fill: rgba(0, 0, 0, 0);
  stroke-width: 1px;
  animation: arrow 1.25s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
  }

  40% {
    opacity: 1;
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes arrow {
  0% {
    opacity: 0;
  }

  10% {
    opacity: .3;
  }

  20% {
    opacity: .5;
  }

  40% {
    opacity: 1;
  }

  60% {
    opacity: .5;
  }

  70% {
    opacity: .3;
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.arrows path.a1 {
  animation-delay: -.5s;
}

.arrows path.a2 {
  animation-delay: -.23s;
}

.arrows path.a3 {
  animation-delay: 0s;
}

@keyframes moveContainer {
  from {
    opacity: .4;
    transform: translateX(-400%)translateY(100%)rotateY(200deg)rotateZ(20deg)rotateX(50deg);
  }

  to {
    opacity: .6;
    transform: translateX(0)translateY(0)rotateY(0deg)rotateZ(0deg)rotateX(0deg);
  }
}

@media screen and (max-width: 420px) {
  body {
    max-width: 320px;
    color: rgba(228, 193, 104, .835);
    max-width: 320px;
    background-color: #000;
