    transition: transform .5s ease-in-out;
  }

  .img-div {
    flex-direction: column;
    margin: auto;
    padding-top: 20px;
    display: flex;
  }

  .info-item {
    justify-content: center;
    display: flex;
  }

  .photo-card {
    max-width: 280px;
    flex-flow: column wrap;
    display: flex;
  }

  .drop-in, .drop-in-2 {
    animation: drop-in 1.7s .1s backwards;
  }

  @keyframes drop-in {
    from {
      opacity: 0;
      transform: scale(0);
    }

